import { User } from "../types/User";

export const loginValidation = (values: User) => {
  const errors : any = {};
  if (!values.email) {
    errors.email = 'Ingresa una direccin de correo electrnico';
  } else if (
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
  ) {
    errors.email = 'Ingresa una direccin de correo electrnico';
  }

  if (!values.password) {
    errors.password = 'Ingresa una contrasea'
  }

  return errors;
}